.title__main {
  letter-spacing: .2;
  text-align: center;
  text-transform: uppercase;
  color: #333742;
  font-size: 67px;
  font-family: 'Butler', Arial, sans-serif;
}

.title__section {
  font-size: 36px;
  letter-spacing: 0.09em;
  line-height: 1.2;
  color: #fff;
  font-family: 'Butler-Light', Arial, sans-serif;
}

.desc_l {
  font-size: 18px;
  line-height: 1.3;
  opacity: .7;
  color: #fff;
  font-family: 'brandon_light', Arial, sans-serif;
}

.desc_s {
  font-size: 18px;
  line-height: 1.3;
  color: #2E2E2E;
  letter-spacing: .01em;
  font-family: 'brandon_light', Arial, sans-serif;
}

.link__more {
  font-size: 18px;
  line-height: 1.3;
  color: #fff;
  letter-spacing: .1em;
  font-family: 'brandon_light', Arial, sans-serif;
}

.link__more:hover svg {
  transform: translateX(10px);
}

.link__more svg {
  height: 12px;
  display: inline-block;
  vertical-align: middle;
  fill: #fff;
  transition: 0.4s;
}

#rightArrow {
  display: inline-block;
  vertical-align: top;
  width: 18px;
  height: 12px;
  fill: #333;
}

@keyframes rotation {
  100% {
    transform: rotate(360deg);
  }
}

* {
  box-sizing: border-box;
  outline: none;
}

*:after, *:before {
  box-sizing: border-box;
}

html ::selection {
  background: rgba(0, 0, 0, 0.2);
  color: #fff;
}

html ::-moz-selection {
  background: rgba(0, 0, 0, 0.2);
  color: #fff;
}

html, body {
  height: 100%;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
}

body {
  color: #000;
  font-size: 18px;
  line-height: 1.3;
  font-family: 'brandon_reg', Arial, sans-serif;
}

a,
button {
  text-decoration: none;
  outline: none;
  border: none;
  cursor: pointer;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0 0 .8em;
  font-weight: normal;
  line-height: 1.1;
}

h1 {
  font-size: 2em;
}

h2 {
  font-size: 1.8em;
}

h3 {
  font-size: 1.6em;
}

h4 {
  font-size: 1.5em;
}

h5 {
  font-size: 1.4em;
}

h6 {
  font-size: 1.3em;
}

b,
strong {
  font-weight: normal;
}

p {
  margin: 0;
}

.list {
  width: 200px;
  margin: 60px auto;
  list-style-type: none;
}

.list__item {
  margin-bottom: 1.3em;
}

.list__link {
  color: #333;
}

.layout {
  position: relative;
  width: 1280px;
  max-width: 100%;
  padding: 0 15px;
  margin: 0 auto;
}

.layout_flex {
  display: flex;
}

.layout__big {
  max-width: 1330px;
}

.layout__right {
  display: inline-block;
  vertical-align: top;
  width: 50%;
  padding: 90px 20px 60px 10%;
  margin-left: auto;
}

.section {
  position: relative;
  min-height: 100vh;
  overflow: hidden;
}

.main-logo .logo-shape {
  stroke: #fffffe;
  stroke-width: .582;
}

.socio__link {
  display: inline-block;
  vertical-align: top;
  padding: 0 10px;
}

.socio__link:first-child {
  padding-left: 0;
}

.socio__link:hover .header__socio_icon {
  fill: #e3f3f6;
}

.socio__icon {
  display: inline-block;
  vertical-align: top;
  height: 24px;
  fill: #315963;
  transition: 0.4s;
}

.subscribe {
  display: block;
  width: 100%;
}

.subscribe__fieldset {
  position: relative;
  width: 100%;
  border: 1px solid #315963;
  margin: 0;
  padding: 0;
  font-size: 0;
}

.subscribe__btn {
  height: 100%;
  width: 120px;
  text-align: center;
  display: inline-block;
  vertical-align: top;
  padding: 10px 0px;
  background-color: #315963;
  color: #fff;
  font-size: 18px;
  font-family: 'brandon_light', Arial, sans-serif;
}

.subscribe__label {
  position: absolute;
  top: 10px;
  left: 12px;
  pointer-events: none;
  user-select: none;
  font-size: 18px;
  color: #6E6E6E;
  font-family: 'brandon_light', Arial, sans-serif;
}

#subscribe__input {
  display: inline-block;
  vertical-align: top;
  width: calc(100% - 120px);
  height: 40px;
  font-size: 18px;
  line-height: 40px;
  border: none;
  padding: 0 0 0 12px;
  margin: 0;
  color: #6E6E6E;
  font-family: 'brandon_light', Arial, sans-serif;
}

.center-text {
  text-align: center;
}

.circle__bg {
  position: absolute;
  top: 0;
  left: 0%;
  width: 50%;
  height: 100%;
  clip-path: circle(100% at 0% 50%);
}

.circle__bg img {
  width: 100%;
}

.clients__bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #f5fafb;
}

.clients__bg_water {
  position: absolute;
  top: 70%;
  left: 80%;
}

.clients__bg_ice {
  position: absolute;
  top: 10%;
  left: -20%;
}

.clients__content {
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
}

.clients__top {
  position: relative;
}

.clients__nav {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  height: 50px;
  transform: translateY(-50%);
  pointer-events: none;
  z-index: 10;
}

.clients__nav .btn {
  pointer-events: auto;
}

.clients .slick-track {
  padding: 20px;
}

.clients__pic {
  position: relative;
  text-align: center;
  opacity: 1;
  transition: 0.3s;
  transform: scale(0.7);
}

.clients__pic.slick-current {
  transform: scale(1);
}

.clients__pic.slick-current img {
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.7);
}

.clients__pic.is-hidden {
  opacity: 0;
  transition: 0.2s;
}

.clients__img {
  width: 200px;
  height: 200px;
  border-radius: 100%;
}

.clients__text {
  display: none;
  opacity: 0;
  transition: 0.6s;
}

.clients__text.slick-current {
  display: block;
  opacity: 1;
}

.clients .btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  padding: 16px;
  background-color: transparent;
}

.clients .btn:hover {
  background-color: #fff;
}

.clients .btn:active .leftArrow {
  transform: translateX(-10px) scale(-1);
}

.clients .btn:active .rightArrow {
  transform: translateX(10px);
}

.clients .btn-prev {
  left: 0;
}

.clients .btn-next {
  right: 0;
}

.clients .leftArrow,
.clients .rightArrow {
  display: inline-block;
  vertical-align: middle;
  width: 18px;
  fill: #000;
  transition: 0.4s;
}

.clients .leftArrow {
  transform: scale(-1);
}

.footer__column_wide {
  flex: 0 1 450px;
}

.footer__heading {
  font-size: 14px;
  text-transform: uppercase;
  color: #6E6E6E;
  letter-spacing: .1;
  line-height: 1.4;
  margin-bottom: 30px;
}

.footer__list {
  padding: 0;
  margin: 20px 0 0;
  list-style: none;
}

.footer__item {
  display: block;
  color: #6E6E6E;
  font-size: 16px;
  line-height: 2;
}

.footer__link {
  color: inherit;
  transition: 0.4s;
}

.footer__link:hover {
  color: #333;
}

.footer .layout {
  width: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 70px 0 50px;
}

.footer .main-logo {
  height: 130px;
  display: inline-block;
  vertical-align: top;
}

.page-header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding: 24px 0;
  border-bottom: 1px solid #fff;
  z-index: 200;
}

.page-header:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 1px;
  background-color: #e3f3f6;
}

.page-header-bg {
  background-color: #fff;
}

.header__logo {
  position: absolute;
  top: -8px;
  left: 50%;
  transform: translateX(-50%);
}

.header__wrap {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__list {
  font-size: 0;
  padding: 0;
  margin: 0;
}

.header__list:first-child {
  margin-right: 50px;
}

.header__list:last-child {
  margin-left: 50px;
}

.header__item {
  display: inline-block;
  position: relative;
  padding: 0 10px;
}

.header__item.is-active .header__link:after {
  content: '';
}

.header__link {
  position: relative;
  font-size: 14px;
  color: #555b5c;
  text-transform: uppercase;
  transition: 0.4s;
}

.header__link:after {
  content: none;
  position: absolute;
  top: 120%;
  left: 30%;
  right: 30%;
  height: 1px;
  background-color: #333;
  transition: 0.4s;
}

.header__link:hover {
  color: #333;
}

.header__btn, .video .btn {
  display: inline-block;
  vertical-align: top;
  text-align: center;
  padding: 10px 30px;
  background-color: #315963;
  font-size: 18px;
  letter-spacing: 1px;
  color: #fff;
  font-family: 'brandon_light', Arial, sans-serif;
  position: relative;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  position: relative;
  transition: color 0.3s ease-out, background-color 0.3s ease-out;
}

.header__btn:hover, .video .btn:hover {
  color: #333333;
  background-color: #fff;
}

.header__btn:hover:before, .video .btn:hover:before {
  -webkit-transform: scaleY(1);
  -ms-transform: scaleY(1);
  transform: scaleY(1);
  box-shadow: 0 0 15px 0px rgba(0, 0, 0, 0.5);
  opacity: 1;
}

.header__btn:before, .video .btn:before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  background: #ffffff;
  -webkit-transform: scaleY(0);
  -ms-transform: scaleY(0);
  transform: scaleY(0);
  -webkit-transform-origin: 50%;
  -ms-transform-origin: 50%;
  transform-origin: 50%;
  opacity: 0;
  -webkit-transition: -webkit-transform 0.3s ease-out, box-shadow 0.3s ease-out, opacity 0.3s ease-out;
  transition: transform 0.3s ease-out, box-shadow 0.3s ease-out, opacity 0.3s ease-out;
  -webkit-transition: -webkit-transform 0.3s ease-out, box-shadow 0.3s ease-out, opacity 0.3s ease-out, -webkit-transform 0.3s ease-out, -webkit-box-shadow 0.3s ease-out;
  transition: transform 0.3s ease-out, box-shadow 0.3s ease-out, opacity 0.3s ease-out, -webkit-transform 0.3s ease-out, -webkit-box-shadow 0.3s ease-out;
}

.resort {
  height: 100%;
  min-height: 100vh;
}

.resort__bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #f5fafb;
}

.resort .layout {
  height: 100%;
  min-height: 100vh;
  padding: 65px 0 90px;
}

.resort .ice {
  position: absolute;
  top: -10px;
  left: 96%;
  display: inline-block;
  vertical-align: top;
  width: 120px;
}

.resort .waterWave {
  position: absolute;
  bottom: 0;
  left: 0;
}

.bubble {
  position: relative;
  border-radius: 100%;
}

.bubble__picture {
  margin-top: 6%;
  left: 26%;
  display: inline-block;
  vertical-align: top;
  font-size: 0;
  height: 46vw;
  width: 46vw;
  max-width: 440px;
  max-height: 440px;
  background-color: #74b7b7;
  z-index: 1;
}

.bubble__picture:after {
  content: '';
  display: inline-block;
  position: absolute;
  top: -60px;
  bottom: -60px;
  left: -60px;
  right: -60px;
  border-radius: 100%;
  border: 1px solid #e7eef0;
  border-right-color: transparent;
  animation: rotation 40s;
}

.bubble__picture_wrap {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 100%;
  overflow: hidden;
  z-index: 10;
}

.bubble__img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-width: 100%;
  min-height: 100%;
}

.bubble__content {
  margin-top: 10%;
  left: 19%;
  display: inline-block;
  vertical-align: top;
  height: 52vw;
  width: 52vw;
  max-width: 560px;
  max-height: 560px;
  padding: 60px 30px 60px 180px;
  background-color: #315963;
}

.bubble__content_small {
  margin-top: 8%;
  left: 10%;
  display: inline-block;
  vertical-align: top;
  text-align: left;
  height: 20vw;
  width: 20vw;
  max-width: 300px;
  max-height: 300px;
  padding: 5vw 30px 60px 40px;
  background-color: #333742;
}

.bubble__content_small p {
  max-width: 100px;
}

.bubble__video {
  margin-top: 2%;
  left: 5%;
  display: inline-block;
  vertical-align: top;
  font-size: 0;
  height: 46vw;
  width: 46vw;
  max-width: 530px;
  max-height: 530px;
  border-radius: 100%;
  background-color: #74b7b7;
  overflow: hidden;
}

.bubble__video iframe {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-height: 100%;
  min-width: 100%;
}

.bubble__text .desc {
  margin-bottom: 1.8em;
}

.bubble .title {
  text-transform: uppercase;
  margin-bottom: 1.5em;
}

.startscreen {
  position: relative;
  text-align: center;
  display: flex;
  height: 100vh;
  text-align: center;
  overflow: hidden;
}

.startscreen__bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.startscreen__half {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 50%;
}

.startscreen__half_left {
  z-index: -1;
}

.startscreen__half_left .startscreen__img {
  left: 100%;
  margin-left: -320px;
}

.startscreen__half_right {
  right: 0;
  width: 60%;
  clip-path: ellipse(26% 51% at 39.3% 76%);
}

.startscreen__half_right:after {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 16.6%;
  width: 1px;
}

.startscreen__half_right .startscreen__img {
  right: calc(100% - 16.6%);
  margin-right: -320px;
}

.startscreen__half_right_top {
  clip-path: ellipse(80% 66% at 97.3% 55%);
}

.startscreen__img {
  position: absolute;
  top: 30%;
  transform-origin: 50% 50%;
  animation: rotation 30s linear infinite;
}

.startscreen-right {
  position: absolute;
  top: 0;
  right: 0;
  width: 40%;
  height: 100vh;
  z-index: -1;
  background-color: #e3f3f6;
}

.startscreen-right:after {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  right: 30%;
  width: 100%;
  height: 100%;
  background-image: url("../img/content/screen-right.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: 0% 50%;
}

.startscreen__list {
  position: absolute;
  bottom: 70px;
  left: 50%;
  transform: translateX(-50%);
  padding: 0;
  margin: 0;
  list-style-type: none;
  font-size: 0;
}

.startscreen__item {
  display: inline-block;
  vertical-align: top;
  padding: 0 20px;
  font-size: 16px;
  color: #000;
}

.startscreen__item:not(:first-child) {
  border-left: 1px solid #333;
}

.startscreen__link {
  font-size: 16px;
  color: #000;
  letter-spacing: 0.14em;
  text-transform: capitalize;
  transition: 0.4s;
}

.startscreen__link:hover {
  text-decoration: underline;
}

.startscreen__nav {
  position: absolute;
  top: 60%;
  left: 30px;
  right: 30px;
  transform: translateY(-50%);
  display: flex;
  align-items: center;
  justify-content: space-between;
  pointer-events: none;
}

.startscreen__nav_link {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 150px;
  height: 150px;
  border: 30px solid #e3f3f6;
  pointer-events: auto;
  border-radius: 100%;
  color: #000;
  font-size: 17px;
  letter-spacing: .12em;
  font-family: 'brandon_light', Arial, sans-serif;
}

.startscreen__nav_link:after {
  content: '';
  position: absolute;
  top: -60px;
  right: -60px;
  left: -60px;
  bottom: -60px;
  border-radius: 100px;
  border: 1px solid #e3f3f6;
  border-bottom-color: transparent;
  transform: rotate(52deg);
  transition: 0.6s;
}

.startscreen__nav_link:hover:after {
  transform: rotate(152deg);
}

.startscreen__nav_right {
  border-color: #fff;
}

.startscreen__nav_right:after {
  border-color: #fff;
  border-top-color: transparent;
}

.startscreen .title__main {
  position: absolute;
  top: 20%;
  left: 50%;
  transform: translateX(-50%);
}

.service, .clients, .video {
  min-height: 100vh;
}

.service__bg, .video__bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #e3f3f6;
}

.service__header {
  margin-bottom: 80px;
}

.service__header .desc {
  display: inline-block;
  width: 100%;
  max-width: 320px;
}

.service__list {
  font-size: 0;
  list-style-type: none;
  padding: 0;
  margin: 0 0 -30px;
  text-align: center;
}

.service__item {
  position: relative;
  display: inline-block;
  vertical-align: top;
  height: 432px;
  width: calc(25% - 30px);
  margin-left: 30px;
  border-radius: 10px;
  box-shadow: 0 0 20px 2px rgba(0, 0, 0, 0.2);
}

.service__item_picture {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.service__item_picture:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 200%;
  background-color: transparent;
  background-image: linear-gradient(transparent, rgba(49, 89, 99, 0.8));
  transition: 1s;
  z-index: 2;
}

.service__item_img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: auto;
  min-height: 100%;
  transition: 1s;
}

.service__item_footer {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  display: block;
  padding: 26px;
  text-align: left;
  z-index: 5;
}

.service__link {
  position: relative;
  display: block;
  height: 100%;
  border-radius: 10px;
  overflow: hidden;
  z-index: 3;
}

.service__link:hover .link__more svg {
  transform: translateX(10px);
}

.service__link:hover .service__item_img {
  transform: translate(-50%, -50%) scale(1.1);
}

.service__link:hover .service__item_picture:before {
  top: -100%;
}

.service .layout, .clients .layout, .video .layout {
  height: 100%;
  min-height: 100vh;
  padding: 65px 0 90px;
}

.service .title__section, .clients .title__section, .video .title__section {
  color: #333742;
  text-transform: uppercase;
  font-family: 'Butler-Light', Arial, sans-serif;
}

.service .main-logo, .clients .main-logo, .video .main-logo {
  position: absolute;
  display: inline-block;
  vertical-align: top;
  height: 40vh;
}

.service .logo-f, .clients .logo-f, .video .logo-f {
  top: -10%;
  fill: #d5e7eb;
}

.service .logo-end, .clients .logo-end, .video .logo-end {
  left: 70%;
  bottom: -10%;
  fill: #d5e7eb;
}

.service .link__more, .clients .link__more, .video .link__more {
  display: inline-block;
  vertical-align: top;
}

.waved .service__item:nth-child(2n) {
  margin-top: 5%;
}

.waved .service__item:nth-child(3n) {
  margin-top: 2%;
}

.waved .service__item:nth-child(4n) {
  margin-top: 6%;
}

.waved .service__item_footer {
  background-color: #fff;
}

.waved .service__item_footer:before {
  content: '';
  position: absolute;
  bottom: 10%;
  left: 0;
  background-color: red;
  width: 53%;
  transform: rotate(153deg);
  height: 100%;
  border-radius: 83px 54%/30% 100%;
}

.waved .service__item .link__more {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #000;
}

.waved .service__item #right-arrow {
  fill: #000;
}

.subheader .layout {
  padding: 90px 0;
}

.subheader .title__main {
  text-transform: uppercase;
  font-size: 56px;
  font-family: 'Butler-Light', Arial, sans-serif;
}

.subheader .desc {
  color: #333742;
  margin-bottom: 1.4em;
}

.next__section {
  position: absolute;
  bottom: 30px;
  left: 55%;
  height: 30px;
}

.next__section img {
  max-height: 100%;
  max-width: 100%;
}

.video .logo-f {
  position: absolute;
  left: 70%;
}

.video .logo-end {
  left: 4%;
}

.video__iframe {
  position: relative;
  display: inline-block;
  width: 640px;
  height: 370px;
  overflow: hidden;
  border-radius: 10px;
  margin-bottom: 60px;
}

.video .btn {
  width: 144px;
}
